.footer {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  padding: 15px 0;
  margin-top: 1.5rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.footer__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
}
.footer__title {
  margin-top: 0.625rem;
}
.footer__copywrite {
  margin-top: 0.625rem;
}